*,
*::before,
*::after {
  margin:  0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Barlow', sans-serif;
}

body {
  line-height: 1.3;
  overflow-x: hidden;
}

label {
  color: #b0b0b0;
  margin-top: 4px;
  font-size: 16px;
}

.full {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
}

input[type='submit'] {
  border: #a55eea;
  color: #a55eea;
  background-color: #fff;
  border: 1px solid #a55eea;
  height: 40px;
  max-width: 100px;
  margin-left: calc(50% - 80px);
  border-radius: 8px;
  font-size: 16px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 8px 16px;
}

input[type='submit']:hover{
  background-color: #a55eea;
  color: #FFF;
  cursor: pointer;
}

@media screen and (max-width: 760px) {
  input[type='submit'] {
    margin-left: 32px;
  }
}

.purple {
  color: 	#a55eea;
  font-weight: 400;
}

.mar {
  margin-top: 32px;
}

